"   La palabra Toráh ha sido traducida como 'Ley'. Una mejor traducción es 'enseñanza; instrucción'. Yahshúa no vino a abrogar la Toráh, pero sí a interpretarla correctamente. Los evangelios son un vivo ejemplo de las re-interpretaciones que Yahshúa hizo de la Toráh. El es la Torá viviente.\n\n   Mashiaj/El Mesías confirmó la naturaleza permanente de la Toráh cuando dijo que El no vino a 'abrogar' la Toráh o los Profetas, sinó a 'cumplir' (Matityahu/Mat. 5:17-20).\n\n   La palabra 'abrogar' viene del griego kataluo que según la Concordancia Strong (Strong's Exhaustive Concordance) significa aflojar, (desintegrar); por implicación, demoler; detener, destruir, echar abajo.\n\n   La palabra 'cumplir' viene del griego pleróo que según Strong significa hacer repleto.\n\n   Según el pensamiento rabínico del Primer Siglo (E.C.) 'abrogar' la Toráh es interpretarla incorrectamente y 'cumplirla' es interpretarla correctamente.  Esto lo vemos claramente cuando en Su función de intérprete de la Toráh, Mashiaj dice:  'oísteis que fue dicho…más Yo os digo...'(Matityahu/Mt. 5:21, 27, 38, 43).\n\n   ¡Mashiaj no vino a quitar la Toráh, el vino más bien a darle su sentido más completo!\n\n   Mashiaj dijo que ni una jota ni una tilde pasará de la Toráh, hasta que todo se haya cumplido. 'Cumplido' aquí viene del griego ginomai, que según Strong significa causar que sea, llegar a ser (venir a la existencia). En otras palabras, NADA de lo que fue dicho en la Toráh quedará sin cumplimiento, TODO acontecerá.  La Palabra de Elohim permanecerá para siempre (Mizmor/Salmo 119:89).\n\n   Rav Shaúl (Pablo) dijo que la fe no invalida la Toráh sino que la confirma (Rom. 3:31). Esto es así porque 'un pacto aunque sea de hombre, una vez ratificado, nadie lo invalida, ni le añade' (Gál. 3:15). De la misma manera que la Toráh [Pacto del Sinaí] no invalidó la Promesa [Pacto de Avraham] como vemos en Gál. 3:17, tampoco el Pacto Renovado, (o Nuevo Pacto) en Mashiaj, invalida la Torá.\n\n   No hay razón alguna por la cual Elohim quisiera abrogar Su Toráh. La Escritura dice que la Toráh es perfecta (Mizmor/Salmo 19:7; Ya´acov/Stgo. 1:25). Mashiaj vino a re-interpretarla y a darnos su sentido más completo.\n\n   Creemos que toda la Palabra de Elohim contiene enseñanza e instrucción (Toráh) para nosotros. Creemos en toda la Toráh (toda la Escritura) según explicada y aplicada por Yahshúa y los apóstoles."
