"   En Romanos 11:17 y 24, el apóstol Pablo nos enseña claramente que los creyentes en Yahshúa que no son de origen judío, son cortados del olivo silvestre (naturaleza gentil) e injertados en el olivo natural (naturaleza israelita), son hechos linaje de Avraham (Gál. 3:29) y reciben los pactos, promesas y la cuidadanía de Israel (Ef. 2:12). Yahshúa eliminó la pared que separa a judíos y no judíos, aboliendo en Su carne las enemistades y las ordenanzas rabínicas que creaban separación entre ambos pueblos para hacerlos uno (Ef. 2:11-19). YHVH nunca creó la separación entre judíos y gentiles, pues la misma Torá establece en Shemot / Éxodo 12:49 (Vayikrá/Lev. 16:29; 17:15; 18:26; 19:34) que hay 'una misma ley tanto para el natural como para el extranjero (gentil)'. Las Escrituras dan fiel testimonio en ambos pactos, que los gentiles siempre han tenido entrada a la cuidadanía israelí. ¡En la mente divina, es el gentil el que se hace israelita, y no al revés! La cultura israelita es la única cultura en el mundo de origen divino; todas las demás culturas son de origen pagano.\n\n   Por todo lo anterior, es nuestra creencia que la adoración en un contexto hebraico es tanto para los israelitas como para los injertados en el olivo natural, que deseen disfrutar de la raíz y de la rica sabia de olivo (Rom. 11:17)."
