"   Bienvenidos a la CONGREGACIÓN MESIÁNICA BETH-EL De Caguas Puerto Rico\n\n\n   Yeshayahu / Isaísas 11:12, registra:  'Y levantarás pendón a las naciones y juntarás los desterrados de Yisrael y reunirá los esparcidos de Yehudá de los cuatro confines de la tierra.' La Congregación Mesiánica Beth-El se funda en el año 1999, en respuesta al proceso iniciado por el Eterno de restaurar la identidad israelita y hacer retornar a casa las Ovejas perdidas de la Casa de Israel y a los esparcidos de la Casa de Judá. Al caducar la fecha del castigo correspondiente sobre cada uno de los Reinos (Reino del Norte y Reino del Sur), como a muchos otros, el Eterno comenzó a revelar Su plan de restauración dejándonos saber que por virtud del pecado de nuestros ancestros perdimos la identidad israelita siendo descendientes de Avraham, Yitzjak y Yaacov.\n\n   Al descubrir el proceso acontecido a través del tiempo de desconexión de la Toráh así como de la cultura y tradiciones de nuestro Pueblo Israel, en Su Misericordia el Eterno ha venido restaurando el entendimiento y conocimiento de Su Toráh y nos ha permitido recuperar la identidad y cultura israelita que habíamos perdido. El entender y descubrir que nuestro Mesías Yahshúa no abrogó ni anuló la Toráh ni los Profetas, ni ninguno de los mandamientos ordenados por el Eterno en Su Toráh (Matituyahu 5:17), sino más bien sus palabras y acciones muestran y revelan el espíritu correcto de ello, nos condujo a estudiar y conocer la Toráh del Eterno y sus mandamientos desde la perspectiva hebrea en la que fue dada y poco a poco hemos venido obedeciendo y aplicando sus mandamientos.\n\n   Somos observantes del Shabbat, Rosh Jodesh y las fiestas bíblicas descritas en Vayikrá /Levítico 23, además de las fiestas judías tradicionales y las ceremonias del ciclo de vida judía como bodas, bar/bat mitzvah, berit miláh, etc.\n\n   La Congregación Mesiánica Beth-El se encuentra bajo la cobertura de Alliance Shomer Yisrael, Inc.  (ASY)"                 
