'   El Shabbat fue instituido y santificado desde la creación (Bereshit/Gén. 2:1-3; Shemot/Ex. 20:8-11) y confirmado por la Toráh de Moisés como Pacto Perpetuo (Shemot/Ex. 31:16-17). Vayikrá/Lev. 23 también presenta las Fiestas Bíblicas como ordenanza perpetua. Por ser estas instituciones perpetuas, las mismas trascienden al tiempo del Milenio. Esto lo confirma la Escritura cuando habla del Templo que se levantará en el Milenio (Yejezkel/Ez. 46:1, 3, 4 y 12), ya que el sacerdocio del Milenio (que operará bajo el gobierno de Mashiaj) enseñará a los creyentes a observar el Shabbat y todas las Fiestas Bíblicas (Yejezkel/Ez. 44:24). La Escritura da testimonio que aun, luego de cielos nuevos y tierra nueva, se seguirá observando el Shabbat (Yeshayahu/Is. 66:22, 23). Otro dato importante con relación al Shabbat es que también es para los gentiles (Yeshayahu/Is. 56:6-7). \n\n   Yahshúa no vino a abrogar los mandamientos de la Toráh (Matityahu/Mt. 5:17). Él observaba el Shabbat (Mt. 12:5-12; Mr. 1:21; 3:1; 6:2; Lucas 4:16; 4:31; 6:6; 13:10; Juan 5: 1 y 9; 9:7 y 14). Las controversias que surgieron con Yahshúa y el Shabbat no estaban relacionadas con el cumplimiento del mandamiento de observar el día de reposo, que está claramente desglosado en la Toráh, sino con relación a las malas interpretaciones que surgieron en torno al Shabbat, que hicieron de éste un día de condenación en vez de el día de refrigerio y descanso que ELOHIM proveyó para el beneficio del hombre.\n\n   El libro de Gevurot / Hechos nos muestra que los apóstoles y discípulos de Yahshúa no se apartaron del Templo, ni de las sinagogas (3:1; 5:12; 26:11), muchos de los cuales eran celosos de guardar los mandamientos de la Toráh (21:20), por tanto podemos concluir que tampoco dejaron de observar el Shabbat como día de adoración y reposo.\n\n   El apóstol Pablo se congregaba en Shabbat (Gevurot / Hechos 13:14 y 42; 13:44; 16:13; 17:2; 18:4). La referencia en Gevurot / Hechos 20:7 a un servicio el primer día de la semana se trata de un servicio que tomó lugar en lo que acostumbramos llamar Havdaláh, a la conclusión del Shabbat (el día sábado en la noche). Según la tradición hebrea, los días comienzan y terminan a la caída del sol. (Bereshit/Gén. 1:5, 8, 13, 19, 23, 31). Después de la caída del sol del Shabbat, comienza el primer día de la semana. Es costumbre hebrea hasta el día de hoy cerrar el Shabbat con un servicio llamado Havdaláh. Este servicio se celebra en la primera hora del primer día de la semana para terminar el Shabbat y comenzar la nueva semana con oración. En la segunda referencia Paulina al primer día de la semana (1 Co. 16:2) el apóstol exhorta a los hermanos a hacer una colecta para los santos en Jerusalén. Esto va muy a tono con la tradición hebrea que no ve con buenos ojos el hacer colectas en Shabbat, por tanto, el apóstol instruye a que se haga la colecta una vez terminado el Shabbat y comenzando el primer día de la semana.\n\n   No hay evidencia alguna en la Biblia que el Shabbat como día de reposo y adoración fuese cambiado para el domingo. Sin duda, algo de tanta importancia habría sido anunciado por Mashiaj y los apóstoles. El testimonio que tanto Yahshúa como el apóstol Pablo nos dan muestra que la controversia presentada en el Nuevo Pacto sobre el Shabbat representa una crítica fuerte y directa en torno a prácticas legalistas; a mandamientos de hombres (ver también Col. 2:16-23) y no a la observación del mandamiento divino. Si aplicamos la primera regla de interpretación bíblica: “La Biblia se interpreta a sí misma” la evidencia demuestra que el “Día del Adón” es y siempre fue el Shabbat (Shemot/Ex. 31:12). ¡El cambio de Shabat a domingo es cuestión histórica, no bíblica!\n\n   En resumen, YHVH no ha cambiado de opinión respecto a Su programa de adoración. En Él no hay mudanza alguna ni sombra de variación (Ya’acov/Stgo. 1:17). El es el mismo ayer, hoy y por los siglos (Gevurot / Hechos 13:8).\n\n\n   (NOTA:  La referencia en Gál. 4: 10, no está relacionada con el Shabbat y las Fiestas Bíblicas establecidas en Vayikrá/Lev. 23. El contexto nos muestra claramente que se trata de celebraciones de origen pagano a las cuales habían vuelto los gálatas (ver los versos del 8-11), los cuales, según Pablo, "empezaron en el espíritu y terminaron en la carne".)'
